<template>
  <div id="makeupCouApplyDiv">
    <div class="blank"></div>
    <div id="tableDiv">
      <table class="operationTable">
        <thead>
          <tr>
            <td class="headTd"></td>
            <td class="headTd"></td>
            <td class="headTd"></td>
            <td class="headTd"></td>
            <td class="headTd"></td>
            <td class="headTd"></td>
            <td class="headTd"></td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(application,index) in applications" :id="index">
            <td>{{ application.teacherName }}</td>
            <td>{{ application.courseName }}</td>
            <td>{{ application.className }}</td>
            <td>{{ application.lessonChangeInfo }}</td>
            <td>{{ application.appTime }}</td>
            <td>{{ application.reason }}</td>
            <td>
              <button @click="setTrue(applications,index,application.index)"></button>
              <!--<button @click="setTrue(index)"></button>-->
              <!---->
              <button @click="setFalse(applications,index,applications.index)"></button>
              <!---->
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'makeupCouApplyDiv',
    data () {
      return {
        applications: [
          /*{ applicationId:'',teacher: '', course: '', className: '20161', courseTime: '12', Type:'', reasonType:'', appTime: '2016.08.01',reason:'??' },
          { applicationId:'',teacher: '', course: '', className: '20172', courseTime: '34', Type:'', reasonType:'', appTime: '2016.08.02',reason:'??' },
          { applicationId:'',teacher: '', course: '', className: '20151', courseTime: '12', Type:'', reasonType:'', appTime: '2016.08.02',reason:'??' },
          { applicationId:'',teacher: '', course: '', className: '20163', courseTime: '7', Type:'', reasonType:'', appTime: '2016.08.02',reason:'??' }*/
        ],
      }
    },
    beforeMount: function() {
      this.$http.post('../testPhp/makeupCouApply.php',{},{
        "Content-Type":"application/json"
      }).then(function(response){
        console.log(":");
        console.log(response.body);
        var data = response.body;
        this.applications = data.applicationsList;
      },function(error){
        console.log("error:");
        console.log(error);
      });
    },
    methods: {
      setTrue: function(applications,index,id){
        //
        if(confirm("")){
          this.$http.post('../testPhp/adjustCouApplySetTrue.php',{
            "index": id,
            "operation": "1"
          },{
            "Content-Type":"application/json"
          }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            if(data.result == "1") {
              applications.splice(index, 1);
            }else{
              alert("");
            }
          },function(error){
            console.log("error:");
            console.log(error);
          });
        }
      },
      setFalse: function(applications,index,id){
        //
        if(confirm("")){
          this.$http.post('../testPhp/adjustCouApplySetFalse.php',{
            "index": id,
            "operation": "0"
          },{
            "Content-Type":"application/json"
          }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            if(data.result == "1") {
              applications.splice(index, 1);
            }else{
              alert("");
            }
          },function(error){
            console.log("error:");
            console.log(error);
          });
        }
      }
    }
  }
</script>

<style scoped>
  html{
  }
  #makeupCouApplyDiv{
    /**/
    margin: 0 auto;
    background-color: #f3f3f3;
    height: 100%;
  }
  #tableDiv{
    margin: 0 5rem;
  }
  button{
    outline: none;
    border: thin solid #A6A6A6;
    background-color: white;
    color: #A6A6A6;
    border-radius: 1rem;
    font-size: 1rem;
    width: 1.45rem;
  }
  button:hover{
    background-color: red;
    color: white;
    border: red;
  }
  @media screen and (max-width:1023px) {
    html {
    }
  }
</style>



