<template>
  <div id="suspendCouApplyDiv">
    <div class="blank"></div>
    <div id="tableDiv">
      <table class="operationTable">
      <thead>
        <tr>
          <td class="headTd"></td>
          <td class="headTd"></td>
          <td class="headTd"></td>
          <td class="headTd"></td>
          <td class="headTd"></td>
          <td class="headTd"></td>
          <td class="headTd"></td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(application,index) in applications" :id="index">
          <td>{{ application.teacherName }}</td>
          <td>{{ application.courseName }}</td>
          <td>{{ application.className }}</td>
          <td>{{ application.courseTime }}</td>
          <td>{{ application.appTime }}</td>
          <td>{{ application.reason }}</td>
          <td>
            <button @click="setTrue(applications,index,application.lessonsChangeId)"></button>
            <!--<button @click="setTrue(index)"></button>-->
            <!---->
            <button @click="setFalse(applications,index,application.lessonsChangeId)"></button>
            <!---->
          </td>
        </tr>
      </tbody>
    </table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'suspendCouApplyDiv',
    data () {
      return {
        applications: [
          /*{ lessonsChangeId:'1', teacherName: '', courseName: '', className: '20161', courseTime: '12', appTime: '2016.08.01',reason:'??' },
          { lessonsChangeId:'2', teacherName: '', courseName: '', className: '20172', courseTime: '34', appTime: '2016.08.02',reason:'??' },
          { lessonsChangeId:'3', teacherName: '', courseName: '', className: '20151', courseTime: '12', appTime: '2016.08.02',reason:'??' },
          { lessonsChangeId:'4', teacherName: '', courseName: '', className: '20163', courseTime: '7', appTime: '2016.08.02',reason:'??' }*/
        ],
      }
    },
    beforeMount: function() {
      this.$http.post('../testPhp/suspendCouApply.php',{
        "lessonsChangeId": 1
      },{
        "Content-Type":"application/json"
      }).then(function(response){
        console.log(":");
        console.log(response.body);
        var data = response.body;
        this.applications = data;
      },function(error){
        console.log("error:");
        console.log(error);
      });
    },
    methods: {
      setTrue: function(applications,index,id){
        //
        if(confirm("")){
          this.$http.post('../testPhp/adjustCouApplySetTrue.php',{
            "lessonsChangeId": id,
            "operation": 1
          },{
            "Content-Type":"application/json"
          }).then(function(response){
            console.log(":");
            console.log(response.body);
            var  data = response.body;
            if(data.result == 1) {
              applications.splice(index, 1);
            }else{
              alert("");
            }
          },function(error){
            console.log("error:");
            console.log(error);
          });
        }
      },
      setFalse: function(applications,index,id){
        //
        if(confirm("")){
          this.$http.post('../testPhp/adjustCouApplySetFalse.php',{
            "lessonsChangeId": id,
            "operation": 0
          },{
            "Content-Type":"application/json"
          }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            if(data.result == 1) {
              applications.splice(index, 1);
            }else{
              alert("");
            }
          },function(error){
            console.log("error:");
            console.log(error);
          });
        }
      }
    }
  }
</script>

<style scoped>
  html{
  }
  #suspendCouApplyDiv{
    /**/
    margin: 0 auto;
    background-color: #f3f3f3;
    height: 100%;
  }
  .blank {
    height: 2.9rem;
  }
  #tableDiv{
    margin: 0 5rem;
  }
  button{
    outline: none;
    border: thin solid #A6A6A6;
    background-color: white;
    color: #A6A6A6;
    border-radius: 1rem;
    font-size: 1rem;
    width: 1.45rem;
  }
  button:hover{
    background-color: red;
    color: white;
    border: red;
  }
  @media screen and (max-width:1023px) {
    html {
    }
  }
</style>



