<template>
  <div id="banCouApplyDiv">
    <div class="blank"></div>
    <div id="tableDiv">
      <table class="operationTable">
        <thead>
          <tr>
            <th class="headTd"></th>
            <th class="headTd"></th>
            <th class="headTd"></th>
            <th class="headTd"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="application in applications" :id="application.forbiddentimeId">
            <td>{{ application.teacherName }}</td>
            <td>{{ application.appContent }}</td>
            <td>{{ application.appTime }}</td>
            <td>
              <button @click="setTrue(applications,index,application.forbiddentimeId)"></button>
              <!---->
              <button @click="setFalse(applications,index,application.forbiddentimeId)"></button>
              <!---->
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'banCouApplyDiv',
    data () {
      return {
        applications: [
          /*{ applicationId:'1', teacherName: '', appContent: '', appTime: '2016.08.01' },
          { applicationId:'2', teacherName: '', appContent: '', appTime: '2016.08.03' },
          { applicationId:'3', teacherName: '', appContent: '', appTime: '2016.08.02' },
          { applicationId:'4', teacherName: '', appContent: '', appTime: '2016.08.05' }*/
        ],
      }
    },
    beforeMount: function() {
      this.$http.post('../testPhp/banCouApply.php',{},{
        "Content-Type":"application/json"
      }).then(function(response){
        console.log(":");
        console.log(response.body);
        var data = response.body;
        this.applications = data;
      },function(error){
        console.log("error:");
        console.log(error);
      });
    },
    methods: {
      setTrue: function(applications,index,id){
        //
        if(confirm("")){
          this.$http.post('../testPhp/adjustCouApplySetTrue.php',{
            "forbiddentimeId": id,
            "operation": 1
          },{
            "Content-Type":"application/json"
          }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            if(data.result == "1") {
              applications.splice(index, 1);
            }else{
              alert("");
            }
          },function(error){
            console.log("error:");
            console.log(error);
          });
        }
      },
      setFalse: function(applications,index,id){
        //
        if(confirm("")){
          this.$http.post('../testPhp/adjustCouApplySetFalse.php',{
            "forbiddentimeId": id,
            "operation": 0
          },{
            "Content-Type":"application/json"
          }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            if(data.result == "1") {
              applications.splice(index, 1);
            }else{
              alert("");
            }
          },function(error){
            console.log("error:");
            console.log(error);
          });
        }
      }
    }
  }
</script>

<style scoped>
  html{
  }
  #banCouApplyDiv{
    /**/
    margin: 0 auto;
    background-color: #f3f3f3;
    height: 100%;
  }
  .blank {
    height: 2.9rem;
  }
  #tableDiv{
    margin: 0 5rem;
  }
  button{
    outline: none;
    border: thin solid #A6A6A6;
    background-color: white;
    color: #A6A6A6;
    border-radius: 1rem;
    font-size: 1rem;
    width: 1.45rem;
  }
  button:hover{
    background-color: red;
    color: white;
    border: red;
  }
  @media screen and (max-width:1023px) {
    html {
    }
  }
</style>



