<template>
  <div id="combineCourseDiv">
    <div id="termDiv" class="blank"></div>
    <div id="tableDiv">
      <table id="t1" class="applyTable">
        <thead>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(information,index) in informations">
            <td class="operationTd" :id="information.id" @click="operationClick(information.id,index)">{{ information.operation }}</td>
            <td>{{ information.course }}</td>
            <td>{{ information.courseId }}</td>
            <td>{{ information.period }}</td>
            <td>{{ information.teacher }}</td>
            <td>{{ information.teacherId }}</td>
            <td>{{ information.className }}</td>
            <td>{{ information.time }}</td>
            <td>{{ information.classroom }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <transition name="editTran">
      <!--transition-->
      <div v-show="editValue" id="editDiv">
        <p id="tipP" style="display: none"></p>
        <div id="weekDiv">
          <span>
            <select id="timeSelect" v-model="time" @change="timeChange()">
              <option disabled></option>
              <option v-for="classTime in classTimes" :value="classTime">{{ classTime }}</option>
            </select>
          </span>
        </div>
        <div id="classroomDiv">
          <span></span>
          <select id="classroomSelect" v-model="classroom">
            <option disabled></option>
            <option v-for="classroom in classrooms" :value="classroom">{{ classroom }}</option>
          </select>
        </div>
      </div>
    </transition>
    <transition name="editTran">
      <div v-show="editValue" id="buttonDiv">
        <button id="saveButton" @click="saveClick" class="am-btn am-btn-success am-radius"></button>
        <button id="restoreButton" @click="restoreClick(tip)" class="am-btn am-btn-success am-radius"></button>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'combineCourseDiv',
    data () {
      return {
        informations: [
          { operation: '', id:"", course: '', courseId: '', period:'', teacher: '', teacherId: '', className: '', time: '', classroom:'' }
         /*{ operation: '', id:"1", course: '', courseId: '111', period:'2', teacher: '', teacherId: '123', className: '20141', time: '12', classroom:'A101' },
          { operation: '', id:"2", course: '', courseId: '666', period:'3', teacher: '', teacherId: '123', className: '20142', time: '56', classroom:'A102' },
          { operation: '', id:"3", course: '', courseId: '222', period:'4', teacher: '', teacherId: '456', className: '20141', time: '34', classroom:'A102' },
          { operation: '', id:"4", course: '', courseId: '333', period:'5', teacher: '', teacherId: '123', className: '20143', time: '12', classroom:'A103' },*/
        ],//
        operationId: '',//
        saveId: '',//
        editValue: false,//
        tip: true,//
        teachers: [
          '', '', '', '', ''
        ],
        classTimes: [],//
        classrooms: [],//
        time:"",
        classroom:"",
        classroomAndTime:[],
        number: 0
      }
    },
    beforeMount: function() {
      this.$http.post('../testPhp/combineCourse.php',{},{
        "Content-Type":"application/json"
      }).then(function(response){
        console.log(":");
        console.log(response.body);
        this.informations = [];
        for(var i = 0;i < response.body.length;i++){
          this.informations.push({"operation":"", "id": response.body[i].operationLessonsId, "course": response.body[i].courseName, "courseId": response.body[i].courseSerial, "period": response.body[i].weekHour, "teacher": response.body[i].teacherName, "teacherId": response.body[i].teacherSerial, "className": response.body[i].classSerial, "time": response.body[i].time, "classroom":response.body[i].classroom});
        }
      },function(error){
        console.log("error:");
        console.log(error);
      });
    },
    methods: {
      operationClick: function(id,index){
        if(this.editValue == true && this.operationId != id){//
          if(confirm("")){
            this.saveClick();
          }else{
            this.restoreClick(false);
            var operationTd = document.getElementById(id);
            this.editValue = false;
            operationTd.innerHTML = "";
          }
        }else{
          this.operationId = id;
        }
        var state = false;
        if(this.informations[index].period > 3){
          var tipP = document.getElementById("tipP");
          tipP.innerHTML = "3";
          tipP.style.display = "none";
        }
        try{
          for(var i = 0;i <= this.informations.length;i++){//
            if(this.informations[i].id != id){
              this.informations[i].operation = "";
            }else{
              if(!state) {
                this.informations[i].operation = "";
                this.editValue = true;
                this.saveId = id;
                this.operationId = id;
                state = true;
              }
            }
          }
        }finally {
          this.$http.post('../testPhp/combineCourseSelect.php', {
            "courseSerial": this.informations[index].courseId,
            "teacherSerial": this.informations[index].teacherId,
            "weekHour": this.informations[index].period,
            "classSerial": this.informations[index].className
          }, {
            "Content-Type": "application/json"
          }).then(function (response) {
            console.log(response.body);
            this.classroomAndTime = response.body;
            console.log(this.classroomAndTime);
            this.classTimes = [];
            for (var i = 0; i < this.classroomAndTime.length; i++) {
              var isExit = false;
              for (var n = 0; n < this.classTimes.length; n++) {
                if (this.classroomAndTime[i].time == this.classTimes[n]) {
                  isExit = true;
                }
              }
              if (!isExit) {
                this.classTimes.push(this.classroomAndTime[i].time);
              }
            }
            console.log(this.classTimes);
          }, function (error) {
            console.log(error);
          });
        }
      },
      saveClick: function(){
//        var editDiv = document.getElementById("editDiv");
        var operationTd = document.getElementById(this.saveId);
//        var saveButton = document.getElementById("saveButton");
        if(this.time != "" && this.time != "" && this.time != null && this.classroom != "") {
          var index = null;
          for (var i = 0; i < this.informations.length; i++) {
            if (this.informations[i].id == this.saveId) {
              index = i;
              break;
            }
          }
          this.$http.post('../testPhp/adjustCouApplySetTrue.php', {
            "operationLessonsId": this.informations[index].id,
            "courseName": this.informations[index].course,
            "teacherName": this.informations[index].teacher,
            "courseSerial": this.informations[index].courseId,
            "teacherSerial": this.informations[index].teacherId,
            "weekHour": this.informations[index].period,
            "classSerial": this.informations[index].className,
            "classroom": this.classroom,
            "time": this.time
          }, {
            "Content-Type": "application/json"
          }).then(function (response) {
            console.log(response.body);
            console.log(this.informations[index]);
            if (response.body.result == 1) {
              this.informations[index].time = this.time;
              this.informations[index].classroom = this.classroom;
              this.restoreClick(false);//
              if(this.informations[index].period > 3){
                if(this.number == 1){
                  this.number = 0;
                  this.editValue = false;
                  operationTd.innerHTML = "";
                  alert("");
                  location.reload(location.href);
                }else{
                  this.number++;
                  alert("");
                }
              }else {
                alert("");
                this.number = 0;
                this.editValue = false;
                operationTd.innerHTML = "";
              }
            } else {
              alert("")
            }
          }, function (error) {
            console.log(error);
          });
        }else{
          alert("");
        }
      },
      restoreClick: function(tip){
        //
//        var timeSelect = document.getElementById("timeSelect");
//        var classroomSelect = document.getElementById("classroomSelect");
        if(tip){
          if(confirm("")){
//            timeSelect.options[0].selected = true;
//            classroomSelect.options[0].selected = true;
            this.time = "";
            this.classroom = "";
          }
        }else{
          this.time = "";
          this.classroom = "";
        }
      },
      timeChange: function(){
        this.classrooms = [];
        this.classroom = "";
        for(var i = 0;i < this.classroomAndTime.length;i++){
          if(this.classroomAndTime[i].time == this.time){
            this.classrooms.push(this.classroomAndTime[i].classroom);
          }
        }
      }
    }
  }
</script>

<style scoped>
  html {
  }
  #combineCourseDiv{
    /**/
    background-color: white;
    text-align: left;
  }
  table{
    text-align: center;
  }
  button:hover{
     cursor: pointer;
  }
  #termDiv{
    display: flex;
    align-items: center;
    padding-left: 5rem;
  }
  #tableDiv{
    background-color: #F0F3F8;
  }
  .operationTd{
    /**/
    text-decoration: underline;
  }
  .operationTd:hover{
    cursor: pointer;
    color: red;
  }
  #editDiv{
    /**/
    display: flex;
    justify-content: space-around;
    min-height: 6rem;
    position: relative;
    margin-top: 1rem;
  }
  #classDiv{
    /**/
    display: flex;
    margin-bottom: 1rem;
  }
  .operationSpan{
    text-decoration: underline;
    margin-left: 0.5rem;
  }
  .operationSpan:hover{
    /**/
    cursor: pointer;
    color: red;
  }
  .selectSpan{
    display: block;
    margin-left: 3.3rem;
    margin-top: 0.5rem;
  }
  #buttonDiv{
     /**/
     display: flex;
     height: 5rem;
     align-items: flex-start;
     justify-content: center;
   }
  #saveButton{
    /**/
    width: 5.6rem;
    margin-right: 1.4rem;
    position: relative;
    top: 2rem;
  }
  #restoreButton{
    /**/
    width: 5.6rem;
    position: relative;
    top: 2rem;
  }
  #restoreButton:active,#saveButton:active{
    background-color: #00a539;
  }
  .editTran-enter-active, .editTran-leave-active {
    /**/
    transition: opacity .9s
  }
  .editTran-enter, .editTran-leave-active {
    /**/
    opacity: 0
  }
  @media screen and (max-width: 1023px) {
    html {
    }
  }
</style>



