<template>
  <div id="classroomManagementDiv">
    <div class="blank"></div>
    <div class="dropDown">
      <div id="classroomDiv">
        <button class="amButtom" @click="classroomClick"><img id="classroomArrow" class="iconImg" :src="icon2"><span class="subtitle"></span></button>
        <!---->
        <table id="classroomTable" v-show="classroomTable">
          <tr class="headTr">
            <td>ID</td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr v-for="(classroom,index) in classrooms" :id="'classroomInputTr'+index" :key="classroom.name">
            <!--indexindexidDOMkey-->
            <td><input id="classroomInput" type="text" v-model="classroom.id" readonly="true"></td>
            <td><input id="classroomInput1" type="text" v-model="classroom.name" readonly="true"></td>
            <td><input id="classroomInput2" type="text" v-model="classroom.number" readonly="true"></td>
            <td class="operationTd">
              <img :id="'classroomEditImg'+index" src="../../../../../assets/images/edit.png" @click="editClick('classroom',index)">
              <!---->
              <img :id="'classroomSaveImg'+index" class="saveImg" src="../../../../../assets/images/save.png" @click="saveClick('classroom',index)">
              <!---->
              <img :id="'classroomRestoreImg'+index" class="restoreImg" src="../../../../../assets/images/restore.png" @click="restoreClick('classroom',index)">
              <!---->
              <img :id="'classroomDeleteImg'+index" src="../../../../../assets/images/delete.png"  @click="deleteClick(classrooms,index)">
              <!---->
            </td>
          </tr>
          <tr>
            <td><img src="../../../../../assets/images/add.png" @click="addClick(classrooms,'classroom')"></td>
            <!--vueinput DOM-->
            <td></td>
            <td></td>
            <td></td>
          </tr>
        </table>
      </div>
      <div id="engineRoomDiv">
        <button class="amButtom" @click="engineRoomClick"><img id="engineRoomArrow" class="iconImg" :src="icon1"><span class="subtitle"></span></button>
        <table id="engineRoomTable" v-show="engineRoomTable">
          <tr class="headTr">
            <td>ID</td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
          <tr v-for="(engineRoom,index) in engineRooms" :id="'engineInputTr'+index" :key="engineRoom.name">
            <td><input id="engineInput" type="text" v-model="engineRoom.id" readonly="true"></td>
            <td><input id="engineInput1" type="text" v-model="engineRoom.name" readonly="true"></td>
            <td><input id="engineInput2" type="text" v-model="engineRoom.number" readonly="true"></td>
            <td class="operationTd">
              <img :id="'engineEditImg'+index" src="../../../../../assets/images/edit.png" @click="editClick('engine',index)">
              <img :id="'engineSaveImg'+index" class="saveImg" src="../../../../../assets/images/save.png" @click="saveClick('engine',index)">
              <img :id="'engineRestoreImg'+index" class="restoreImg" src="../../../../../assets/images/restore.png" @click="restoreClick('engine',index)">
              <img :id="'engineDeleteImg'+index" src="../../../../../assets/images/delete.png" @click="deleteClick(engineRooms,index)">
            </td>
          </tr>
          <tr>
            <td><img src="../../../../../assets/images/add.png" @click="addClick(engineRooms,'engine')"></td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'classroomManagementDiv',
    data () {
      return {
        icon1:require('../../../../../assets/images/icon1.png'),
        icon2:require('../../../../../assets/images/icon2.png'),
        classroomArrow: true,
//        
        engineRoomArrow: false,
//        
        classroomTable: true,
//        
        engineRoomTable: false,
//        
        classrooms: [
          /*{ id:"1-101", name:"101", number:"60" },
          { id:"1-102", name:"102", number:"60" },
          { id:"1-103", name:"104", number:"130" },
          { id:"1-202", name:"202", number:"60" },
          { id:"1-203", name:"203", number:"60" },
          { id:"1-206", name:"206", number:"60" },
          { id:"1-207", name:"207", number:"60" },
          { id:"1-209", name:"209", number:"60" },
          { id:"1-211", name:"211", number:"60" },
          { id:"2-202", name:"202", number:"70" },
          { id:"2-203", name:"203", number:"70" },
          { id:"2-302", name:"302", number:"70" },
          { id:"2-303", name:"303", number:"70" },
          { id:"2-304", name:"304", number:"140" },
          { id:"2-305", name:"305", number:"140" },
          { id:"2-306", name:"306", number:"60" },
          { id:"2-404", name:"404", number:"140" },
          { id:"2-405", name:"405", number:"140" },
          { id:"2-406", name:"406", number:"60" },*/
        ],
//        
        engineRooms: [
//          { id:"", name:"402", number:"70" },
//          { id:"", name:"403", number:"70" },
        ],
//        
        buffer_classrooms: [],
        buffer_engineRooms: []
      }
    },
    beforeMount: function() {
//      this.$http.post('../testPhp/classroomMgmt.php',{},{
      this.$http.post('./classroomManage/getClassroomInfo',{},{
        "Content-Type":"application/json"
      }).then(function(response){
        console.log(":");
        console.log(response.body);
        var isJson = null;
        var data = null;
        try {
          json(response.body);
          isJson = true;
        }catch (e) {
          isJson = false;
        }
        if(isJson){
          data = json(response.body);
        }else{
          data = response.body;
        }
        for(var i = 0;i < data.Classroom.length;i++){
          if(data.Classroom[i].classType == ""){
            this.engineRooms.push({ id:data.Classroom[i].classroomId, name:""+data.Classroom[i].classroomId.split("-")[0]+""+data.Classroom[i].classroomId.split("-")[1], number:data.Classroom[i].classroomSize });
          }else{
            this.classrooms.push({ id:data.Classroom[i].classroomId, name:""+data.Classroom[i].classroomId.split("-")[0]+""+data.Classroom[i].classroomId.split("-")[1], number:data.Classroom[i].classroomSize });
          }
        }
        for(var i = 0;i < this.classrooms.length;i++){
          this.buffer_classrooms.push({ id:"", name:"", number:"" });
        }
        for(var i = 0;i < this.engineRooms.length;i++){
          this.buffer_engineRooms.push({ id:"", name:"", number:"" });
        }
      },function(error){
        console.log("error:");
        console.log(error);
      });
    },
    methods: {
      classroomClick: function(){
//        
        var classroomArrow = document.getElementById("classroomArrow");
        if(!this.classroomArrow){
//              
          this.classroomArrow = true;
          this.classroomTable = true;
          classroomArrow.src = this.icon2;
        }else{
//              
          this.classroomArrow = false;
          this.classroomTable = false;
          classroomArrow.src = this.icon1;
        }
      },
      engineRoomClick: function(){
//        
        var engineRoomArrow = document.getElementById("engineRoomArrow");
        if(!this.engineRoomArrow){
          this.engineRoomArrow = true;
          this.engineRoomTable = true;
          engineRoomArrow.src = this.icon2;
        }else{
          this.engineRoomArrow = false;
          this.engineRoomTable = false;
          engineRoomArrow.src = this.icon1;
        }
      },
      editClick: function(type,index){
//        
        var inputTr = document.getElementById(type+"InputTr"+index);
        var input = inputTr.getElementsByTagName("input");
        var editImg = document.getElementById(type+"EditImg"+index);
        var saveImg = document.getElementById(type+"SaveImg"+index);
        var restoreImg = document.getElementById(type+"RestoreImg"+index);
        var deleteImg = document.getElementById(type+"DeleteImg"+index);
        var i = null;
//        for(i = 0;i<input.length;i++){
//          
//          if(i != 1) {
        input[2].readOnly = false;
        input[2].style.border = "0.1rem solid #d4d4d9";
//          }
//        }
        if(input[0].value == ""){
          input[0].readOnly = false;
          input[0].style.border = "0.1rem solid #d4d4d9";
        }
        if(type == "classroom"){
          this.buffer_classrooms.splice(index, 1, JSON.parse(JSON.stringify(this.classrooms[index])));
        }
        if(type == "engine"){
          this.buffer_engineRooms.splice(index, 1, JSON.parse(JSON.stringify(this.engineRooms[index])));
        }
        editImg.style.display = "none";
        deleteImg.style.display = "none";
        saveImg.style.display = "inline";
        restoreImg.style.display = "inline";
//        ,
      },
      restoreClick: function(type,index){
//        ,,
        if(confirm("")){
          var inputTr = document.getElementById(type+"InputTr"+index);
          var input = inputTr.getElementsByTagName("input");
          var editImg = document.getElementById(type+"EditImg"+index);
          var saveImg = document.getElementById(type+"SaveImg"+index);
          var restoreImg = document.getElementById(type+"RestoreImg"+index);
          var deleteImg = document.getElementById(type+"DeleteImg"+index);
          var i = null;
          console.log(this.buffer_classrooms[index]);
          console.log(this.classrooms[index]);
          if(type == "classroom"){
            this.classrooms.splice(index, 1, JSON.parse(JSON.stringify(this.buffer_classrooms[index])));
          }
          if(type == "engine"){
            this.engineRooms.splice(index, 1, JSON.parse(JSON.stringify(this.buffer_engineRooms[index])));
          }
          for(i = 0;i<input.length;i++){
//          
            input[i].readOnly = true;
            input[i].style.border = "none";
          }
          editImg.style.display = "inline";
          deleteImg.style.display = "inline";
          saveImg.style.display = "none";
          restoreImg.style.display = "none";
        }
      },
      deleteClick: function(type,index){
        var classroom = null;
        if (type == "classroom") {
          classroom = this.classrooms;
        }
        if (type == "engine") {
          classroom = this.engineRooms;
        }
        if(confirm("")){
//          this.$http.post('../testPhp/classroomMgmtSave.php',{
          this.$http.post('./classroomManage/deleteClassroom',{
            "classroomId": classroom[index].id
          },{
            "Content-Type":"application/json"
          }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            if(data.result == "1"){
              //          data
              if (type == "classroom") {
                this.classrooms.splice(index, 1);
                this.buffer_classrooms.splice(index, 1);
              }
              if (type == "engine") {
                this.engineRooms.splice(index, 1);
                this.buffer_engineRooms.splice(index, 1);
              }
            }else{
              alert("");
            }
          },function(error){
            console.log("error:");
            console.log(error);
          });
        }
      },
      saveClick: function(type,index){
//        
        if(confirm("")){
          var inputTr = document.getElementById(type+"InputTr"+index);
          var input = inputTr.getElementsByTagName("input");
          var editImg = document.getElementById(type+"EditImg"+index);
          var saveImg = document.getElementById(type+"SaveImg"+index);
          var restoreImg = document.getElementById(type+"RestoreImg"+index);
          var deleteImg = document.getElementById(type+"DeleteImg"+index);
          var i = null;
          var classType = null;
          if(type == "classroom"){
            classType = "";
          }else if(type == "engine"){
            classType = "";
          }
          if(input[0].readOnly = false){
            if(isNaN(Number(input[2].value))){
              alert("");
            }else{
//              this.$http.post('../testPhp/classroomMgmtSave.php', {
            this.$http.post('./classroomManage/addClassroom', {
                "classroomId": input[0].value,
                "classType": classType,
                "classroomSize": input[2].value
              }, {
                "Content-Type": "application/json"
              }).then(function (response) {
                console.log(":");
                console.log(response.body);
                var data = response.body;
                if (data.result == "1") {
                  if (type == "classroom") {
                    var name = "" + this.classrooms[index].id.split("-")[0] + "" + this.classrooms[index].id.split("-")[1];
                    this.classrooms.splice(index, 1, {"id":input[0].value,"name":name,"number":input[2].value});
                  }
                  if (type == "engine") {
                    var name = "" + this.engineRooms[index].id.split("-")[0] + "" + this.engineRooms[index].id.split("-")[1];
                    this.engineRooms.splice(index, 1, {"id":input[0].value,"name":name,"number":input[2].value});
                  }
//          ,data,,
                  for (i = 0; i < input.length; i++) {
                    if (i != 1) {
                      input[i].readOnly = true;
                      input[i].style.border = "none";
                    }
                  }
                  editImg.style.display = "inline";
                  deleteImg.style.display = "inline";
                  saveImg.style.display = "none";
                  restoreImg.style.display = "none";
                }
              }, function (error) {
                console.log("error:");
                console.log(error);
              });
            }
          }else{
            if(input[0].value.indexOf("-") < 0){
              alert("ID1-101");
            }else if(isNaN(Number(input[0].value.split("-")[0])) || isNaN(Number(input[0].value.split("-")[1]))){
              alert("ID1-101");
            }else if(isNaN(Number(input[2].value))){
              alert("");
            }else{
//              this.$http.post('../testPhp/classroomMgmtSave.php', {
            this.$http.post('./classroomManage/editClassroom', {
                "classroomId": input[0].value,
                "classType": classType,
                "classroomSize": input[2].value
              }, {
                "Content-Type": "application/json"
              }).then(function (response) {
                console.log(":");
                console.log(response.body);
                var data = response.body;
                if (data.result == "1") {
                  if (type == "classroom") {
                    var name = "" + this.classrooms[index].id.split("-")[0] + "" + this.classrooms[index].id.split("-")[1];
                    this.classrooms.splice(index, 1, {"id":input[0].value,"name":name,"number":input[2].value});
                  }
                  if (type == "engine") {
                    var name = "" + this.engineRooms[index].id.split("-")[0] + "" + this.engineRooms[index].id.split("-")[1];
                    this.engineRooms.splice(index, 1, {"id":input[0].value,"name":name,"number":input[2].value});
                  }
//          ,data,,
                  for (i = 0; i < input.length; i++) {
                    if (i != 1) {
                      input[i].readOnly = true;
                      input[i].style.border = "none";
                    }
                  }
                  editImg.style.display = "inline";
                  deleteImg.style.display = "inline";
                  saveImg.style.display = "none";
                  restoreImg.style.display = "none";
                }
              }, function (error) {
                console.log("error:");
                console.log(error);
              });
            }
          }
        }
      },
      addClick: function (classroom,type){
//        if(classroom[classroom.length - 1].id != ""){
          classroom.push(
              { id:"", name:"", number:"" }

          );
          if (type == "classroom") {
            this.buffer_classrooms.push(
                { id:"", name:"", number:"" }
            );
          }
          if (type == "engine") {
            this.buffer_engineRooms.push(
                { id:"", name:"", number:"" }
            );
          }
//        }else{
//          alert("");
//        }
      }
    }
  }
</script>

<style scoped>
  #classroomManagementDiv{
    /*Div*/
    margin: 0 auto;
    background-color: #f3f3f3;
    min-height: 39rem;
  }
  .dropDown{
    margin: 0.5rem 5rem;
  }
  /*.classroomButton{
    !**!
    width: 100%;
    text-align: left;
    display: flex;
    align-items: center;
    outline: none;
    background-color: #37937A;
    border: thin solid #37937A;
    margin: 0.1rem;
    color: white;
  }
  .classroomButton:hover{
    cursor: pointer;
  }*/
  img{
    position: relative;
    margin: 0.5rem 0.2rem;
    width: 1.5rem;
    height: 1.5rem;
  }
  table{
    width: 100%;
    margin: 0 auto;
    border-collapse: collapse;
    border-right: thin solid #E3E3E3;
    border-left: thin solid #E3E3E3;
  }
  td{
    border-bottom: thin solid #E3E3E3;
    height: 2.5rem;
    text-align: center;
  }
  input{
    outline:none;
    border: none;
    text-align: center;
  }
  img:hover{
    cursor: pointer;
  }
  .saveImg{
    /**/
    display: none;
  }
  .restoreImg{
    /**/
    /*margin: 0 0 0 0.1rem;*/
    display: none;
  }
  .operationTd{
    /**/
    width: 15rem;
  }
  @media screen and (max-width: 1023px) {
    html {
    }
  }
</style>



