<template>
    <div id="checkCourseDiv">
        <div id="operationDiv">
            <div id="selectDiv">
                <select id="termSelect" v-model="termSelect">
                    <option disabled></option>
                </select>
                <select id="weekSelect" v-model="weekSelect">
                    <option disabled></option>
                </select>
                <button id="queryButton" @click="queryCourseClick()"  class="am-btn am-btn-success am-radius"></button>
            </div>
        </div>

        <div id="mainDiv">
            <p id="tableTipP"></p>
            <p id="tableInfoP">{{ term }} {{ week }} </p>
            <tableDiv :queryCourse="queryCourse"></tableDiv><!---->
        </div>
    </div>
</template>
<!--js-->
<script>
    import tableDiv from '../tableDiv/tableDiv.vue'
    export default {
        name: 'checkCourseDiv',
        data () {
            return {
                term: '',//
                week: '',
                termSelect: '',
                weekSelect: '',
                queryCourse: []
            }
        },
        components: {
            tableDiv
        },
        methods: {
            queryCourseClick: function(){
                this.$http.post('./acdeminSeeCurriculum',{
//                this.$http.post('../testPhp/checkCourseQuery.php',{
                    "year": "2016-2017",
                    "term": "1",
                    "week": "1"
                },{
                    "Content-Type":"application/json"
                }).then(function(response){
                    console.log(":");
                    console.log(response.body);
                    this.queryCourse = response.body.course;
                },function(error){
                    console.log("error:");
                    console.log(error);
                });
            }
        }
    }
</script>

<style scoped>
    #checkCourseDiv{
        background-color: #f3f3f3;
    }
    #mainDiv{
        /**/
        background-color: white;
        margin: 0 5rem;
        min-height: 33rem;
    }
    select{
        margin-right: 1.4rem;
    }
    #operationDiv{
        background-color: white;
        margin: 0 0 0.6rem;
    }
    #selectDiv{
        padding: 0.6rem 5rem;
    }
    #tableTipP{
        /**/
        color: #C1C1C1;
        position: relative;
        text-align: left;
        z-index: 2;
        padding: 1rem;
        margin: 0;
    }
    #tableInfoP{
        /**/
        text-align: left;
        position: relative;
        margin-top: 0;
        left: 1rem;
    }
    @media screen and (max-width:1023px) {
        html {
        }
    }
</style>



