<template>
  <!---->
  <div id="login" :style="{ backgroundImage: 'url(' + img1 + ')' }">
    <!--css-->
    <div id="loginDiv" :style="{ backgroundImage: 'url(' + img2 + ')' }">
      <a href="http://www.samsph.com/hsxx/1092/1/"><img id="schoolImg" src="./images/schoolLogo.png" :alt="imgAlt" ></a>
      <p id="hospitalMottoP">   </p>
      <p id="loginTitleP"></p>
      <span id="loginTipSpan"><span id="tipSpan" @click="modal1 = true"></span></span>
      <div id="userNumDiv" class="inputDiv">
        <span class="inputSpan"></span><br/>
        <input id="userNumInput" class="loginInput" type="text" v-model="userId">
      </div>
      <div id="PasswordDiv" class="inputDiv">
        <span class="inputSpan"></span><br/>
        <input id="passwordInput" class="loginInput" type="password" v-model="passwordValue">
      </div><br/>
      <div>
        <!--<input id="rememberCheckbox" type="checkbox"><label id="rememberLabel" for="rememberCheckbox"></label>-->
      </div><br/>
      <div>
        <button id="loginButton" @click="loginClick" class="am-btn am-btn-success am-radius"></button>
        <span id="forgetSpan" @click="forgetPwClick"></span>
      </div>
    </div>
    <Modal
        v-model="modal1"
        width="400"
        id="modalBody">
      <div slot="header" style="font-size: 1rem;text-align: center;padding: 0.5rem 0;" id="modalHeader">
        <span></span>
      </div>
      <div style="font-size: 0.9rem;text-align: left;">
        <p>123456</p>
        <p>123456</p>
        <p>123456</p>
      </div>
      <div slot="footer" style="text-align: center">
        <button id="modalBtn" @click="ok1"></button>
        <button id="modalBtn" @click="cancel1"></button>
      </div>
    </Modal>
  </div>
</template>

<script>
    var CryptoJS = require("crypto-js");
    export default {
        name: 'login',
        data () {
            return {
              imgAlt: '',//
              img1: require('../../../assets/images/login-background.png'),//
              img2: require('./images/loginDiv.png'),//
              userId: '',//
              passwordValue: '',//
              modal1: false//
            }
        },
        mounted: function() {
          var dom = document.getElementById("login");
          dom.style.height = window.innerHeight + "px";
        },
        methods: {
          ok1: function(){
            this.modal1 = false;
          },
          cancel1: function(){
            this.modal1 = false;
          },
          loginClick: function(){
//            
            if(this.userNumValue == "" || this.passwordValue == "" ) {
              alert("");
            }else{
              var a = CryptoJS.MD5(this.passwordValue + this.userId + "");
              a = a.toString().toUpperCase();
              function encrypt(data) {
                var key  = CryptoJS.enc.Latin1.parse('uestc2017nurse01');
                var iv   = CryptoJS.enc.Latin1.parse('10esrun7102ctseu');
                return CryptoJS.AES.encrypt(data, key, {iv:iv,mode:CryptoJS.mode.CBC,padding:CryptoJS.pad.ZeroPadding}).toString();
              }
//              this.$http.post('../testPhp/loginCheck.php', {
              this.$http.post('./login', {
                "userId": this.userId,
                "loginPwd": JSON.stringify(encrypt(a))
              }, {
                "Content-Type": "application/json"
              }).then(function (response) {
                console.log(response.body);
                if(response.body.result == "1"){
                  location.href = '#/eduAdmin/adjustCouApply';
                }else{
                  alert("");
                }
              }, function (error) {
                alert(",");
                console.log(error);
              });
            }
          },
          forgetPwClick: function(){
//            
            location.href='#/forgetPassword';
          }
        }
    }
</script>

<style scoped>
    html{
    }
    #login{
      /**/
      text-align: center;
      font-family: ;
      background-repeat: no-repeat;
      height: 46rem;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    #loginDiv{
      /**/
      width: 50%;
      height: 70%;
      background-repeat: no-repeat;
      background-size: cover;
      box-shadow: 0 0 0.5rem;
      display: flex;
      flex-direction: column;
      padding: 2rem 3rem;
      text-align: left;
    }
    #schoolImg{
      /**/
      width: 50%;
      height: 15%;
    }
    #hospitalMottoP{
      /**/
      color: #34B064;
    }
    #loginTitleP{
      /**/
      font-size: 1.4rem;
    }
    #loginTipSpan{
      /**/
      color: #727272;
      font-size: 0.8rem;
      position: relative;
      bottom: 1rem;
    }
    .inputDiv{
      /*div*/
      border: none;
      text-align: left;
      height: 4rem;
      width: 15rem;
    }
    .loginInput{
      /**/
      outline: none;
      border: thin solid #CECECE;
      height: 2.5rem;
      width: 15rem;
      font-size: 1rem;
    }
    #tipSpan{
      /**/
      color: #00A64A;
    }
    #tipSpan:hover{
      cursor: pointer;
    }
    .inputSpan{
      /**/
      position: relative;
      top: 0.7rem;
      left: 1.5rem;
      background-color: white;
    }
    #rememberCheckbox{
      /**/
      outline: none;
    }
    #rememberLabel{
      /**/
      font-size: 0.7rem;
    }
    #loginButton{
      /**/
      min-width: 5rem;
      min-height: 2.3rem;
      width: 8rem;
    }
    #loginButton:hover{
      cursor: pointer;
    }
    #loginButton:active{
      background-color: green;
    }
    #forgetSpan{
      /**/
      font-size: 0.75rem;
      position: relative;
      left: 1rem;
    }
    #forgetSpan:hover{
      cursor: pointer;
    }
    @media screen and (max-width:1200px) {
        #loginDiv {
          background-size: 100% 100%;
          height: 65%;
        }
    }
</style>



